import React from "react";
import "./Navbar.css";
function Navbar() {
  return (
    <div>
      <div className="container-fluid">
        <div className="row">
          <div className="col-12">
            <nav className="navbar navbar-expand-lg py-0 bg-light">
              <div className="container-fluid">
                <a className="navbar-brand"  href="/#">
                  Navbar
                </a>
                <div
                  className="collapse navbar-collapse"
                  id="navbarSupportedContent"
                >
                  <ul className="navbar-nav me-5 font-weight-bold ms-auto mb-lg-0">
                    <li className="nav-item">
                      <a className="nav-link active" aria-current="page"  href="/#">
                        Home
                      </a>
                    </li>
                    <li className="nav-item">
                      <a className="nav-link  active"  href="/#">
                        About Us
                      </a>
                    </li>
                    <li className="nav-item dropdown">
                      <a
                        className="nav-link active dropdown-toggle"
                         href="/#"
                        role="button"
                        data-bs-toggle="dropdown"
                        aria-expanded="false"
                      >
                        our Courses
                      </a>
                      <ul className="dropdown-menu">
                        <li>
                          <a className="dropdown-item active"  href="/#">
                            Action
                          </a>
                        </li>
                        <li>
                          <a className="dropdown-item"  href="/#">
                            Another action
                          </a>
                        </li>
                        <li>
                          <hr className="dropdown-divider" />
                        </li>
                        <li>
                          <a className="dropdown-item"  href="/#">
                            Something else here
                          </a>
                        </li>
                      </ul>
                    </li>
                    <li className="nav-item">
                      <a href="/#" className="nav-link active ">FAQ</a>
                    </li>
                    <li className="nav-item">
                      <a href="/#" className="nav-link active ">Contact Us</a>
                    </li>
                  </ul>
                    <button className="btn btn-outline-warning me-2 font-weight-bold" type="submit">
                      <b> Class Join Now</b>
                    </button>
                    <button className="btn trial btn-warning" type="submit">
                    <b> Book for Free Trial</b>
                    </button>
                </div>
              </div>
            </nav>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Navbar;
